<template>
    <div>
        <el-upload
            multiple
            v-bind="uploadInfo"
            :show-file-list="false"
            :on-success="handleSuccess"
            :on-exceed="handleExceed"
            :before-upload="beforeAvatarUpload"
            list-type="picture"
            :file-list="fileList">
            <el-button size="small" type="primary">{{ btnText }}</el-button>
            <div slot="tip" class="el-upload__tip">{{ uploadTip }}</div>
        </el-upload>
        <ul class="upload-list" v-if="fileType === 'pic'">
            <li
                v-for="img in imgList"
                :key="img.url"
                :style="uploadStyle"
                :class="['avatar-wrapper', 'el-upload-list__item', focusing ? 'focusing' : '', 'el-upload-list--picture-card']"
                @focus="focusing = true"
                @blur="focusing = false"
                @click="focusing = false">
                <img
                    :src="img.url"
                    :style="avatarStyle">
                <span class="el-upload-list__item-actions">
                    <span
                        class="el-upload-list__item-delete"
                        @click="onRemovePic(img)">
                        <i class="el-icon-delete"></i>
                    </span>
                </span>
            </li>
        </ul>
        <ul class="upload-list" v-else>
            <li
                v-for="img in imgList"
                :key="img.url"
                :style="uploadStyle"
                :class="['upload-list-item']"
                @focus="focusing = true"
                @blur="focusing = false"
                @click="focusing = false">
                <a :href="img.url" class="upload-list-text">{{img.name}}</a>
                <i class="el-icon-close" @click="onRemovePic(img)"></i>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import { interfaceUrl } from '@/api/config'

export default {
    name: 'MultiUpload',
    props: {
        upload: {
            type: Object,
            default: () => ({})
        },
        btnText: {
            type: String,
            default: '点击上传',
            desc: '上传按钮名称'
        },
        uploadTip: {
            type: String,
            default: '支持jpg，jpeg，png，gif格式，且不超过2MB',
            desc: '上传限制提示信息'
        },
        sizeLimit: {
            type: String,
            default: '2M',
            desc: '上传图片大小限制'
        },
        imgW: {
            type: Number,
            default: 178,
            desc: '图片显示组件的宽度'
        },
        imgH: {
            type: Number,
            default: 178,
            desc: '图片显示组件的高度'
        },
        fileList: {
            type: Array,
            default: () => ([]),
            desc: '已上传的文件列表'
        },
        fileType: {
            type: String,
            default: 'pic',
            desc: '上传的文件类型，pic或者text'
        }
    },
    data () {
        return {
            focusing: false,
            imgList: []
        }
    },
    computed: {
        uploadStyle () {
            return this.fileType === 'pic' ? {
                width: this.imgW + 'px',
                height: this.imgH + 'px',
                lineHeight: this.imgH + 'px'
            } : {}
        },
        avatarStyle () {
            return {
                maxWidth: this.imgW + 'px',
                maxHeight: this.imgH + 'px'
            }
        },
        uploadInfo () {
            return Object.assign({
                action: interfaceUrl + '/api/httpH5/upload/6999',
                data: {
                    updateUid: this.userInfo.uid
                },
                accept: 'image/gif, image/jpeg, image/jpg, image/png',
                name: 'multiFile',
                limit: 5
            }, this.upload)
        },
        maxSize () {
            // 返回sizeLimit整数
            const sizeLimit = this.sizeLimit
            let unit = 'M'
            let foundUnit = sizeLimit.match(/(M|m)(B|b)*/)
            if (!foundUnit) {
                foundUnit = sizeLimit.match(/(K|k)(B|b)*/)
                if (foundUnit) {
                    unit = 'K'
                } else {
                    // 传递参数有问题的时候
                    unit = ''
                    console.log('参数SizeLimit传递有错误，传递的单位非K或者M')
                }
            }
            // 如果传递的数据不正确，就返回默认的2M
            return foundUnit ? {
                unit,
                size: sizeLimit.substring(0, foundUnit.index).trim()
            } : {
                unit: 'M',
                size: 2
            }
        },
        ...mapState({
            userInfo: state => state.userInfo
        })
    },
    watch: {
        fileList: {
            immediate: true,
            handler (val) {
                this.imgList = val.slice()
            }
        }
    },
    methods: {
        beforeAvatarUpload (file) {
            let exceedSize = false
            if (this.maxSize.unit === 'K') {
                exceedSize = file.size / 1024 > this.maxSize.size
            } else {
                exceedSize = file.size / (1024 * 1024) > this.maxSize.size
            }
            if (exceedSize) {
                this.$message.error(`上传图片大小不能超过 ${this.maxSize.size}${this.maxSize.unit}B!`)
            }
            return !exceedSize
        },
        handleSuccess (res, file, fileList) {
            // TODO：由于后端业务错误返回200，通过code判断异常
            // eslint-disable-next-line
            if (res.code != 200) {
                this.$confirm(res.message, '提示信息').catch(() => { })
            } else {
                this.imgList.push({
                    name: file.name,
                    url: res.data.accessUrl
                })
                // TODO: 这里可能需要回传到父组件中整个URL
                this.$emit('back-event', {
                    imageList: this.imgList
                })
            }
        },
        handleExceed (files, fileList) {
            this.$message({
                message: `上传图片大于${this.uploadInfo.limit}张`,
                type: 'warning'
            })
        },
        onRemovePic (img) {
            this.imgList.splice(this.imgList.indexOf(img), 1)
            this.$emit('back-event', {
                imageList: this.imgList
            })
        }
    }
}
</script>

